






















import fIcon from '@/components/m-icon/f-icon.vue'
import {mapState, mapGetters} from 'vuex'

export default {
	components: {fIcon},
	data() {
		return{
			member:{
				id:0,
				name:'',
				avatarUrl:''
			},
			// qrcode:'data:image/jpeg;base64,',
			qrcode:''
		}
	},
	computed: {
		...mapState(['hasLogin', 'account']),
		...mapGetters(['agentMember'])
	},
	methods:{
		enter:function(url){
			uni.navigateTo({
				url:'../statement/'+url
			})
		},
		getQrcode:function(){
			let that = this;
			uni.request({
				url:this.$http.contextPath + 'account/qrcode',
				method:'POST',
				data:{
					id:this.agentMember.id,
					accountId:this.account.id
				},
				success(res) {
					if(that.$http.success(res)){
						that.qrcode = 'data:image/jpeg;base64,'+res.data.msg.replace(/[\r\n]/g, "");
						console.log(that.qrcode);
					}
				}
			})
		}
	},
	onLoad() {
		Object.assign(this.member, this.agentMember);
		this.getQrcode();
	}
}
